<template>
  <div class="col-12">
      <br>
      <div class="row">
        <div class="col-1"></div>
        <div class="col-2 mr-4">
            <div class="box box1">
                <img src="@/assets/usuarios.png" alt="">
                <h2><b>Usuarios</b></h2>
                
                <div class="container-p">
                    <p><center> {{NumUsuarios}}  Usuarios registrados.</center></p>
                </div>                   
            </div>
        </div>
        <div class="col-2 mr-4 ml-4">
            <div class="box box1">
                <img src="@/assets/sucursal.png" alt="">
                <h2><b>Sucursales</b></h2>
                <div class="container-p">
                    <p><center> {{NumSucursales}} Sucursales registradas.</center></p>
                </div>
            </div>
        </div>
        <div class="col-2 mr-4 ml-4">
            <div class="box box1">
                <img src="@/assets/productos.png" alt="">
                <h2><b>Productos</b></h2>
                <div class="container-p">
                    <p><center> {{NumProductos}} Productos registrados.</center></p>
                </div>
            </div>
        </div>
        <div class="col-2 mr-4 ml-4">
            <div class="box box1">
                <img src="@/assets/proveedores.png" alt="">
                <h2><b>Proveedores</b></h2>
                <div class="container-p">
                    <p><center> {{NumProveedores}} Proveedorees registrados.</center></p>
                </div>
            </div>
        </div>  
      </div>
  </div>
</template>

<script>
import axios from '../../config/axiosHome'

export default {
    name: 'panelGeneral',
    data() {
        return {
            NumSucursales: 0,
            NumUsuarios: 0,
            NumProductos: 0,
            NumProveedores: 0
        }
    },
    mounted() {
        this.backProviders();
        this.backUsers();
        this.backSucursal();
        this.backProducts();
    },
    methods: {
        backProviders() {
            axios.get('/Proveedores/listar')
            .then(response => {
                    this.NumProveedores = response.data.length;
            })
            .catch(
                    error => console.log(error)
            );
        },
        backUsers() {
            axios.get('/Usuarios/listar')
            .then(response => {
                    this.NumUsuarios = response.data.length;
            })
            .catch(
                    error => console.log(error)
            );
        },
        backSucursal() {
            axios.get('/Sucursales/listar')
            .then(response => {
                    this.NumSucursales = response.data.length;
            })
            .catch(
                    error => console.log(error)
            );
        },
        backProducts() {
            axios.get('/Productos/listar')
            .then(response => {
                    this.NumProductos = response.data.length;
            })
            .catch(
                    error => console.log(error)
            );
        }
    }
}
</script>

